.chatForm{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    & input,& textarea {
        width: 300px;
        padding: 5px;
        margin-bottom: 10px;
        border: 1px solid gray;
    }
    & textarea {
        height: 150px;
        resize: none;
    }
}